import React, { useState } from 'react'
import Readmore from  '../readmore/Readmore';
import './about.css'

import laptop from '../images/aboutImages/laptop.jpg'

import { CSSTransition } from 'react-transition-group';
import { Context } from '../context/Context';

const   About = () => {

    const [showCv, setShowCv] = useState(false);//cv  kapalı

    return (
             <React.Fragment>
                        <CSSTransition  in={showCv} timeout={500} classNames="alert" unmountOnExit >
                            <Readmore setShowCv={setShowCv}  /> 
                        </CSSTransition>
            <Context.Consumer>
             {value => (
              
                        <section id="about" className="about  " style={{ color:`${value.darkTheme ?'#f6f7f9' :'#2A3050'}`, backgroundColor:`${value.darkTheme ?'#2A3050' :'#f6f7f9'}`}}  >
                            <div className="container py-4">

                                    <div className="row justify-content-center">
                                        <div className="col-lg-8  ">
                                            <div className="about-title text-center">
                                                <h2 className="fw-bold my-5">About Me</h2>
                                            </div>
                                        </div>
                                    </div>
                            
                                    <div className="row  align-items-center align-content-center">
                                        <div className="col-md-6 first">
                                            <div className="about-text  text-end">
                                                <h3>Lorem ipsum dolor sit amet consectetur adipisicing elit. Deleniti odio ipsam quo totam enim.</h3>
                                                <p className="pt-3">Lorem iagni optio officia inventore reprehenderit aspernatur quidem, in, unde repudiandae aliquam cupiditate culpa sequi explicabo. Repellat ipsam nisi necessitatibus accusamus facere. Numquam deleniti tempora, corporis distinctio impedit aperiam illum exercitationem quaerat ab esse ad odio magnam molestiae?</p>
                    {/*popup ı tetikleyen buton*/}<button   onClick={() => setShowCv(true)} className="btn btn-primary  mb-4 px-4 ">Read More..</button>  
                                            </div>
                                        </div>
                                        <div className="col-md-6 mt-3 second">
                                            <div className="about-img  text-center ">
                                                <img  src={laptop} className="img-fluid w-75 shadow-lg"/>
                                            </div>
                                        </div>
                                    </div>
                               </div>
                        </section>
                           
             )}    
             </Context.Consumer>    
        </React.Fragment>
    )
}

export default About
