import React, { useEffect, useRef} from 'react'
import './home.css';
import { init } from 'ityped';
import { MdKeyboardArrowDown } from 'react-icons/md';

//import images
import berkay from "../images/homeImages/my-photo.jpg"

//get darkTheme
import { Context } from '../context/Context';



function Home() {

    const textRef = useRef();//span tagını aldım ref olarak
    //efekt kullan
 useEffect(() => {
    console.log(textRef)
    init(textRef.current,{
         showCursor: false, 
         backDelay:1500,//silmeyi beklemenin gecikmesi
         showCursor: true,
         backSpeed: 60,
         strings: ['Fron End Developer', 'React Developer', 'Designer']
      })
 },[])
 





    return (   
        <Context.Consumer>
        
        {value => (

                                       /* tüm section component başların py-5 */ 
        <section id="home" className="home  " style={{backgroundColor:`${value.darkTheme ?'#1e2239' :'white'}`}}>
             <div className="container">                  {/*hizala öğeleri merkeze(vertical)*/}
                 <div className="row min-vh-100 align-items-center align-content-center">

                     <div className="col-md-6  mt-md-0">
                         <div className="home-img  text-center">                            {/*mw-100 max-widtf:100% */}
                             <img src={berkay} className="rounded-circle"  />
                         </div>
                     </div>

                     <div className="col-md-6  mt-3  mt-md-0 ">{/*  order-md-first - col ların yerlerini değiştirir */}
                         <div className="home-text">
                           <h2 className=" text-capitalize " style={{color:`${value.darkTheme ?'white' :'gray'}`}}>Hi there, I'm</h2>
                           <h1 className="text-primary text-uppercase fw-bold ">Berkay Oral</h1>
                           <h3  style={{color:`${value.darkTheme ?'white' :'gray'} `}}>Freelance <span className="text-capitalize text-primary fw-bold " ref={textRef}></span> </h3>
                           <a className="arrowIcon" href="#about"> <MdKeyboardArrowDown className="arrowDownIcon"/> </a>
                         </div>
                     </div>

                 </div>
             </div>
        </section>

)}
        
        </Context.Consumer>
    )
}

export default Home
