import React from 'react'

function HireMe() {
    return (
        <section id="hire-me" className="hire-me bg-primary py-5 ">
            <div className="container py-4">
                <div className="row justify-content-center my-4">
                    <div className="col-lg-8 text-center">
                        <h2 className="text-capitalize fs-1 text-white ">Do you want to have a website ?</h2>
                        <a href="#contact" className="btn btn-outline-light fs-4 mt-4 ">Hire Me</a>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default HireMe
