import React, { useState, useEffect } from 'react'
import './slider.css';
import {softwareData} from "./Slidercontent";



const SliderArea = () => {

    const  [softwares, setsoftwares] = useState(softwareData);
    const  [index, setindex] = useState(0);

  

    const goNext = () => {
               if(index < softwares.length -1){
                   setindex( index  +  1 )
                }
                else{
                    setindex( 0 )
                }
    }

    const goPrev = () => {
         if(index <=  softwares.length -1){
            setindex( index - 1 )
         } 
         if(index <= 0){
             setindex( softwares.length -1)
         }
    }
  
     let timer;

     useEffect(() => {
        timer = setInterval(() => {
            goNext();
      }, 2500);


     
        return () => clearInterval(timer);
   
      });
    
      const stopSlider = () => {
          clearInterval(timer);
      }

      const goSlider = () => {
        timer = setInterval(() => {
            goNext();
      }, 2500);

      }

        return (

            <section id="slider" className="sldr  py-5 ">

             <div className="slider-title">
                 <h2 className="text-center text-capitalize text-white fw-bold my-5">I use these</h2>
             </div>
            
            <div className="slider p-0 my-0" onMouseOver={stopSlider} onMouseLeave={goSlider}>

                 <div className="arrows-container clearfix">
                    <i onClick={goPrev} className="fa fa-angle-left  prevIcon"></i> 
                    <i onClick={goNext} className="fa fa-angle-right nextIcon"></i>
                 </div>

                                 {/* kaç slide var ise 100 ona bölersin ( 5 slide var 100 / 5 20) //card 'ın genişiliği kadar 20% */}
                        <div className="slides " style={{transform:`translateX(${ index * -20}%)`}}>
                        {softwares.map(softwareItem => (
                               <div className="card  justify-content-center align-items-center" key={softwareItem.id}>
                                    <div className="softwareLogos pb-4" style={{maxWidth:"185px", maxHeight:"185px"}}>
                                            <img style={{width:"100%", height:"100%", objectFit:"contain"}}  className="img-fluid  mx-auto" src={softwareItem.image}/>
                                    </div>
                                        <h4 className="text-primary fw-bold fs-1 p-0 m-0">{softwareItem.name}</h4>
                              </div>  
                        ))}


                </div>


            </div>

            </section>
        )
    
}

export default SliderArea;
